:root {
    --cream: #DBDFAC;
    --red: #af1125;
    --purple: #3B1F2B;
    --grey: #425163;
    --blue: #383961;
}

.ai-image-generator {
    display: flex;
    flex-direction: column;
    margin: auto;
    align-items: center;
    margin-top: 80px;
    margin-bottom: 20px;
    gap: 30px;
}

.header {
    font-size: 70px;
    font-weight: 500;
    padding-bottom: 30px;
}

.header span {
    color: var(--red);
    font-weight: 600;
}

.img-loading {
    display: flex;
    flex-direction: column;
}

.img-loading img {
    width: 512px;
}

.search-box {
    display: flex;
    width: 1000px;
    height: 95px;
    justify-content: space-around;
    align-items: center;
    border-radius: 50px;
    background: var(--grey);
}

.search-input {
    width: 600px;
    height: 50px;
    background: transparent;
    border: none;
    outline: none;
    font-size: 18px;
    color: white;
    padding-left: 35px;
    margin-right: 35px;
}

.search-input::placeholder {
    color: #999;
}

.generate-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 300px;
    height: 85px;
    font-size: 20px;
    border-radius: 50px;
    background: var(--red);
}

.loading-bar {
    width: 0px;
    height: 8px;
    background: #de1b89;
}

.loading-text {
    font-size: 18px;
}

.loading-bar-full {
    width: 512px;
    height: 8px;
    background: #de1b89;
    transition: 15s;
}

.display-none {
    display: none;
}

